.courses-layout h2.category-title {
  margin-top: 50px; }

.courses-layout .course-item {
  margin-top: 30px; }

.course-logo {
  margin-bottom: 50px;
  width: 550px; }

.small-img {
  width: 650px; }

.mini-img {
  width: 300px; }

#footer {
  margin-top: 50px;
  padding-top: 150px;
  padding-bottom: 100px;
  background-color: #222;
  color: #ddd; }
  #footer a {
    color: #eee; }
  #footer a:hover {
    color: #999;
    text-decoration: none; }
  #footer a.uk-icon-button {
    color: #333; }
  #footer .footer-logo img {
    -webkit-filter: invert(100%);
    -moz-filter: invert(100%);
    -o-filter: invert(100%);
    -ms-filter: invert(100%);
    filter: invert(100%); }
  #footer .footer-logo a {
    opacity: 0.8; }
    #footer .footer-logo a:hover {
      opacity: 1; }
