// Part of vaseman-site file.
@import url(http://fonts.googleapis.com/css?family=Oswald:300,400,300,700);
@import url(http://fonts.googleapis.com/css?family=Arvo:400,700);
@import url(http://fonts.googleapis.com/css?family=Roboto:300,400,500);
@import url(http://fonts.googleapis.com/css?family=Open+Sans:300,400,500);

// Mixins
// ----------------------------------
@mixin transition($dur) {
  transition: all $dur;
}

@mixin body-font() {
  font-family: 'Roboto', /*'Xin4',*/ 'Heiti TC', '微軟正黑體', sans-serif;
}

@mixin title-font() {
  font-family: 'Oswald', 'Adobe 繁黑體 Std', 'AdobeFanHeitiStd-Bold', 'LiHei Pro', "Xin6", '微軟正黑體', sans-serif;
}

// Global
// ----------------------------------
html, body {
  @include body-font();
  font-size: 17px;
  line-height: 175%;
}

body {
  // padding-top: 40px; /* 40px to make the container go all the way to the bottom of the topbar */
}
h1,
h2,
h3,
h4,
h5,
h6 {
  @include title-font();
  margin-bottom: 1em;
  line-height: 120%;
}

a {
  @include transition(.5s);
}

a:hover {
  @include transition(.5s);
}

