// Basic page
.basic-layout {

  h1, h2, h3 {
    text-align: center;
  }

  article.article-content {
    margin-top: 50px;
  }
}
.side-menu.uk-nav .uk-nav-header {
  font-size: 1.3em;
  @include title-font();
}

// Courses page
.courses-layout {
  h2.category-title {
    margin-top: 50px;
  }

  .course-item {
    margin-top: 30px;
  }
}

.course-logo {
  margin-bottom: 50px;
  width: 550px;
}
.small-img {
  width: 650px;
}
.mini-img {
  width: 300px;
}

#footer {
  margin-top: 50px;
  padding-top: 150px;
  padding-bottom: 100px;
  background-color: #222;
  color: #ddd;

  a {
    color: #eee;
  }
  a:hover {
    color: #999;
    text-decoration: none;
  }

  a.uk-icon-button {
    color: #333;
  }

  .footer-logo img {
    -webkit-filter: invert(100%);
    -moz-filter: invert(100%);
    -o-filter: invert(100%);
    -ms-filter: invert(100%);
    filter: invert(100%);
  }

  .footer-logo a {
    opacity: 0.8;
    &:hover {
      opacity: 1;
    }
  }
}

